@import "../../../common/styles/mixins";

.profilePage {
  @include page();

  .container {
    @include container();

    .profile {
      @include block(0);

      h1 {
        @include title();
      }

      .avatar {
        display: block;
        margin: 30px auto 0;
        max-height: 96px;
        max-width: 96px;
        width: 100%;
        height: 100%;
      }

      .name {
        display: block;
        text-align: center;
        font-weight: 500;
        font-size: 20px;
        line-height: 24px;
        margin-top: 17px;
        height: 31px;
      }

      .email {
        @include note();
        text-align: center;
        margin-top: 14px;
      }

      .btn {
        display: block;
        padding: 8px 20px;
        border: none;
        background-color: #FCFCFC;
        border-radius: 20px;
        margin: 29px auto 0;
        box-shadow: 0 5px 5px #9b9b9b;
        font-weight: 500;
        font-size: 16px;
        line-height: 20px;
        letter-spacing: 0.01em;
        font-family: inherit;
        cursor: pointer;
        transition: background-color .2s linear;

        &:hover {
          background-color: #efeeee;
        }
      }
    }
  }
}
